/* 16. pagination */

.woocommerce-pagination .page-numbers {
	display      : -webkit-box;
	display      : -ms-flexbox;
	display      : block;
	padding-left : 0;
	list-style   : none;
	border-radius: 0;
}

.woocommerce-pagination .page-numbers .page-numbers {
	display: inline-block;
	margin : 0 5px;
}

.woocommerce-pagination .page-numbers .page-numbers {
	border       : none;
	height       : 50px;
	width        : 50px;
	display      : block;
	line-height  : 40px;
	background   : #ffc010;
	border-radius: 50%;
	color        : #fff;
	font-size    : 14px;
	text-align   : center;
}

.woocommerce-pagination .page-numbers .page-numbers:hover {
	box-shadow: 0px 16px 32px 0px rgba(255, 74, 87, 0.2);
	background: #010f2e;
	color     : #fff;
}

.woocommerce-pagination .page-numbers .page-numbers.current {
	background: #010f2e;
	color     : #ffffff;
}

.pagination-wrap .pagination {
	display      : block;
	border-radius: unset;
}

.pagination-wrap .pagination li {
	display      : inline-block;
	margin-right : 6px;
	margin-left  : 0;
	margin-bottom: 0;
}

.pagination-wrap .pagination li.active a {
	background: linear-gradient(rgba(216,166,42,255) 20%,rgba(248,215,114,255) 80%);;
	color     : #ffffff;
	box-shadow: 0px 8px 16px 0px rgba(26, 35, 126, 0.32);
}

.pagination-wrap .pagination li a {
	border       : none;
	height       : 50px;
	width        : 50px;
	display      : block;
	line-height  : 50px;
	background   : transparent -webkit-linear-gradient(left, #ffc010, #ffc01010) 0% 0% no-repeat padding-box;
	background   : transparent linear-gradient(90deg, #ffc010, #ffc01010) 0% 0% no-repeat padding-box;
	border-radius: 50%;
	color        : #fff;
	font-size    : 14px;
	text-align   : center;
}

.pagination-wrap .pagination li a:hover {
	color     : #ffffff;
	background: linear-gradient(rgba(216,166,42,255) 20%,rgba(248,215,114,255) 80%);;
}

