.pro-filter .nice-select {
	-webkit-tap-highlight-color: transparent;
	background-color           : #fff;
	border-radius              : 0;
	border                     : solid 1px #e8e8e8;
	box-sizing                 : border-box;
	clear                      : both;
	cursor                     : pointer;
	display                    : block;
	float                      : left;
	font-family                : inherit;
	font-size                  : 14px;
	font-weight                : normal;
	height                     : 50px;
	line-height                : 48px;
	outline                    : none;
	padding-left               : 18px;
	padding-right              : 30px;
	position                   : relative;
	text-align                 : left !important;
	-webkit-transition         : all 0.2s ease-in-out;
	transition                 : all 0.2s ease-in-out;
	-webkit-user-select        : none;
	-moz-user-select           : none;
	-ms-user-select            : none;
	user-select                : none;
	white-space                : nowrap;
	width                      : auto;
}

.pro-filter:before {
	content    : "\f107";
